<template>
  <v-navigation-drawer
    app
    v-model="isShowed"
    :mini-variant="isMinimized"
    @transitionend="isTransEnded = true"
  >
      <!-- :expand-on-hover="expandOnHover"
      :permanent="permanent"
      :src="bg" -->
    <!-- <v-list-item>
      <v-list-item-content v-show="!isMinimized">
        <v-list-item-title class="title">
          Application
        </v-list-item-title>
        <v-list-item-subtitle>
          subtext
        </v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item> -->
    <v-btn x-large icon @click="isMinimized = !isMinimized">
      <v-icon large>mdi-menu</v-icon>
    </v-btn>
      <!-- class="my-2" -->
    <!-- <v-overflow-btn
      v-model="lang"
      :items="langs"
      label="Toggle menu"
    /> -->

    <v-divider/>
    
    <v-menu transition="slide-y-transition">
      <template v-slot:activator="{ on }">
        <v-btn x-large icon v-on="on">
          <img style="height:2em; width:2em; border-radius:50%" :src="`imgs/flag_${lang}.svg`"/>
        </v-btn>
      </template>
      <v-list>
        <v-list-item v-for="l in langs" :key="l" link @click="changeLang(l)">
          <v-list-item-icon>
            <img style="height:1.5em; width:1.5em; border-radius:50%" :src="`imgs/flag_${l}.svg`"/>
          </v-list-item-icon>
          <v-list-item-title v-text="{en:'English', ru: 'Русский'}[l]"/>
        </v-list-item>
      </v-list>
    </v-menu>

    <v-btn x-large icon active-class="primary--text" to="/settings">
      <v-icon>mdi-cog</v-icon>
    </v-btn>

    <v-divider/>

    <v-list>
      <v-list-item-group color="primary">
        <v-list-item link to="/play">
          <v-list-item-icon>
            <v-icon>mdi-gamepad</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="$t('play')"/>
          </v-list-item-content>
        </v-list-item>
        <v-list-item link to="/editor">
          <v-list-item-icon>
            <v-icon>mdi-pen</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="$t('edtr')"/>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>

    <v-divider/>

    <v-list>
      <v-list-item-group color="primary">

        <v-list-item link to="/">
          <v-list-item-icon>
            <v-icon>mdi-home</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="$t('main')"/>
          </v-list-item-content>
        </v-list-item>

        <v-list-item link to="/wiki">
          <v-list-item-icon>
            <v-icon>mdi-information</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="$t('wiki')"/>
          </v-list-item-content>
        </v-list-item>

        <v-list-item link to="/news">
          <v-list-item-icon>
            <v-icon>mdi-newspaper</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="$t('news')"/>
          </v-list-item-content>
        </v-list-item>

      </v-list-item-group>
    </v-list>

    <v-divider/>
    
    <template v-slot:append>
      <v-footer v-show="isTransEnded && !isMinimized" class="grey--text text-caption text-center">
        Undefined Inc. All right is not reserved © 2020
      </v-footer>
    </template>
  </v-navigation-drawer>
</template>

<script>
import {langs} from '../plugins/i18n.js'
// import { mapState } from 'vuex'

export default {
  data() {
    return {
      langs,
      lang: this.$store.getters.lang,
      isShowed: true,
      isMinimized: false,
      isTransEnded: true,
      spage: null,
    };
  },
  // computed: {},
  watch: {
    isMinimized(){
      this.isTransEnded = false;
    }
  },
  // mounted(){},
  methods: {
    changeLang(key){
      this.lang = key;
      this.$store.dispatch('setLocale', key);
    },
  },
  i18n: {
    // sharedMessages: commonMessages,
    messages: {
      en: {
        play: 'Play',
        edtr: 'Editor',
        main: 'Home',
        wiki: 'Wiki',
        news: 'News',
      },
      ru: {
        play: 'Играть',
        edtr: 'Редактор',
        main: 'Главная',
        wiki: 'Вики',
        news: 'Новости',
      }
    },
  }
}
</script>

<style>
</style>