
// const prefix = process.env.LOCAL_STORAGE_KEY_PREFIX || '';
const prefix = 'w';
let ai = 0;

function Mail() {
  this.k = prefix + (ai++).toString(36);
}
Mail.prototype.save = function(v){ localStorage.setItem(this.k, v) }
Mail.prototype.load = function(){ return localStorage.getItem(this.k) }

export default {
  language: new Mail,
  play__last_tab: new Mail,
}