import Memory from '../helpers/memory';
import {i18n, langs} from '../plugins/i18n'

export function setLocale({ commit, dispatch, getters }, lang = getters.lang) {
  if(!langs.includes(lang)) lang = langs[0];
  
  return import(`../locales/${lang}.json`)
    .then(data => {
      i18n.setLocaleMessage(lang, data.default);
      i18n.locale = lang;
      document.querySelector('html').setAttribute('lang', lang);
      // axios.defaults.headers.common['Accept-Language'] = lang
      Memory.language.save(lang);
      commit('mut', {lang});
    })
    // .catch(e=>{
    //   console.error('Не удалось загрузить файл локализации языка "'+lang+'": ', e)
    // })
}